.nav-toggle{
   background: #171719;
   width:30px;
   height: 30px;
   position: absolute;
   top: 30px;
   right: 30px;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   -webkit-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;
   z-index: 2;
}
.nav-toggle-bar{
   background: orange;
   width: 100%;
   height: 2px;
   position: absolute;
   left: 0;
   -webkit-transition:all .5s;
   transition:all .5s;
}
.nav-toggle-bar::before{
   background: orange;
   width: 100%;
   height: 2px;
   position: absolute;
   left: 0;
   content:"";
   top: -10px;
   -webkit-transition:all .5s;
   transition:all .5s;
}
.nav-toggle-bar::after{
   background: orange;
   width: 100%;
   height: 2px;
   position: absolute;
   left: 0;
   content:"";
   top: 10px;
   -webkit-transition:all .5s;
   transition:all .5s;
}
